
import { defineComponent, onMounted, ref,  } from 'vue';
import { useRouter } from 'vue-router';
import { ILogin, ISignup } from '@/types/Interfaces';
import {formRequired} from '@/mixins/FormValidator';

import useAuth from '@/types/Auth';

export default defineComponent({
  components: {
  },
  setup() {
    const router = useRouter();
    
    const {
      token,
      sendSignupEmail
    } = useAuth();

    const formData = ref({
      email: '',
    });
    
    const form = ref<HTMLFormElement | null>(null);

    const formError = ref<string>('');
    
    const setFromError = (message: string) => {
      formError.value = message;
      window.setTimeout(() => {
        formError.value = '';
      }, 5000);  
    };
    
    onMounted(() => {
      token.value = null;
    });

    const loading = ref(false);

    const onSubmit = async (event: Event) => {
      // validate
      if (form.value) {
        const isValid = form.value.checkValidity();
        if (!isValid) {
          alert('not vaida')
          return;
        }
      }
      try {
        loading.value = true;
        const data = await sendSignupEmail(formData.value);
        if (data.is_send) {
          router.push({ name: 'Signup3', params: { email: formData.value.email } });  
        } else {
          alert(data.message)
        }
      } catch (err) {
        console.log(err)
        if (err.response) {
          const { status, data } = err.response;
          console.log(data)
         
          setFromError(JSON.stringify(data))
         
        } else {
          setFromError('ネットワークエラー')
        }
        
      }
      loading.value = false;
      
    };
    return {
      router,
      loading,
      form,
      formRequired,
      formData,
      formError,
      // formValid,
      onSubmit
    };
  }
})
